	module.exports = {"pathContext":{"docHtml":"<h1>homedir-polyfill <a href=\"https://www.npmjs.com/package/homedir-polyfill\"><img src=\"https://img.shields.io/npm/v/homedir-polyfill.svg?style=flat\" alt=\"NPM version\"></a> <a href=\"https://npmjs.org/package/homedir-polyfill\"><img src=\"https://img.shields.io/npm/dm/homedir-polyfill.svg?style=flat\" alt=\"NPM downloads\"></a> <a href=\"https://travis-ci.org/doowb/homedir-polyfill\"><img src=\"https://img.shields.io/travis/doowb/homedir-polyfill.svg?style=flat&#x26;label=Travis\" alt=\"Linux Build Status\"></a> <a href=\"https://ci.appveyor.com/project/doowb/homedir-polyfill\"><img src=\"https://img.shields.io/appveyor/ci/doowb/homedir-polyfill.svg?style=flat&#x26;label=AppVeyor\" alt=\"Windows Build Status\"></a></h1>\n<blockquote>\n<p>Node.js os.homedir polyfill for older versions of node.js.</p>\n</blockquote>\n<h2>Install</h2>\n<p>Install with <a href=\"https://www.npmjs.com/\">npm</a>:</p>\n<pre><code class=\"language-sh\">$ npm install --save homedir-polyfill\n</code></pre>\n<h2>Usage</h2>\n<pre><code class=\"language-js\">var homedir = require('homedir-polyfill');\nconsole.log(homedir());\n//=> /Users/doowb\n</code></pre>\n<h2>Reasoning</h2>\n<p>This library is a polyfill for the <a href=\"https://nodejs.org/api/os.html#os_os_homedir\">node.js os.homedir</a> method found in modern versions of node.js.</p>\n<p>This implementation tries to follow the implementation found in <code>libuv</code> by finding the current user using the <code>process.geteuid()</code> method and the <code>/etc/passwd</code> file. This should usually work in a linux environment, but will also fallback to looking at user specific environment variables to build the user's home directory if neccessary.</p>\n<p>Since <code>/etc/passwd</code> is not available on windows platforms, this implementation will use environment variables to find the home directory.</p>\n<p>In modern versions of node.js, <a href=\"https://nodejs.org/api/os.html#os_os_homedir\">os.homedir</a> is used.</p>\n<h2>About</h2>\n<h3>Related projects</h3>\n<p><a href=\"https://www.npmjs.com/package/parse-passwd\">parse-passwd</a>: Parse a passwd file into a list of users. | <a href=\"https://github.com/doowb/parse-passwd\" title=\"Parse a passwd file into a list of users.\">homepage</a></p>\n<h3>Contributing</h3>\n<p>Pull requests and stars are always welcome. For bugs and feature requests, <a href=\"../../issues/new\">please create an issue</a>.</p>\n<p>Please read the <a href=\"contributing.html\">contributing guide</a> for avice on opening issues, pull requests, and coding standards.</p>\n<h3>Building docs</h3>\n<p><em>(This document was generated by <a href=\"https://github.com/verbose/verb-generate-readme\">verb-generate-readme</a> (a <a href=\"https://github.com/verbose/verb\">verb</a> generator), please don't edit the readme directly. Any changes to the readme must be made in <a href=\".verb.html\">.verb.md</a>.)</em></p>\n<p>To generate the readme and API documentation with <a href=\"https://github.com/verbose/verb\">verb</a>:</p>\n<pre><code class=\"language-sh\">$ npm install -g verb verb-generate-readme &#x26;&#x26; verb\n</code></pre>\n<h3>Running tests</h3>\n<p>Install dev dependencies:</p>\n<pre><code class=\"language-sh\">$ npm install -d &#x26;&#x26; npm test\n</code></pre>\n<h3>Author</h3>\n<p><strong>Brian Woodward</strong></p>\n<ul>\n<li><a href=\"https://github.com/doowb\">github/doowb</a></li>\n<li><a href=\"http://twitter.com/doowb\">twitter/doowb</a></li>\n</ul>\n<h3>License</h3>\n<p>Copyright © 2016, <a href=\"https://github.com/doowb\">Brian Woodward</a>.\nReleased under the <a href=\"LICENSE\">MIT license</a>.</p>\n<hr>\n<p><em>This file was generated by <a href=\"https://github.com/verbose/verb-generate-readme\">verb-generate-readme</a>, v0.2.0, on October 19, 2016.</em></p>","docPath":"argo-ci/node_modules/homedir-polyfill/readme","proj":"argo-ci"}}

